.header{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    padding: 30px, 10%;
    background: rgba(255, 255, 255, 0.411);
    height: 70px;
    /* width: max-content; */
}

.logo{
    max-width: 134px;
    height: 40px;
    margin-left: 10px;
    text-align: center;
    font-size: 26px;
}

.loginBtn{
    
    border: 2px solid black;
    color: blanchedalmond;

}

.navLinks{
    list-style: none;
}

.navLinks li{
    display: inline-block;
    padding: 0px, 20px;
}

.eachLink{
    text-decoration: none;
    transition: all 0.3s ease 0s;
    padding-left: 30px;
    color: #2a2524;
}

.eachLink:hover{
    color: #fd6702;  /* Cambiar acá el color de efecto de los botones*/
}


li, .searchBar{
        font-family: "Montserrat", sans-serif;
        font-weight: 500;
        font-size: 16px;
    color: #fd6702; /* Cambiar acá el color de las letras*/
    text-decoration: none;
}

.search{
    display: flex;
    align-items: center;
}

.fav, .cart{
    padding-right: 40px;
}

.fav{
    padding-left: 40px;
}