.create{
    background-image: url('https://i.pinimg.com/originals/18/f1/84/18f184a49e25dd94769fd30530f78adf.jpg');
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    grid-template-rows: repeat(6, 1fr);
}

